        {{ toUpperTextKey('value_round') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('value_round'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:[`header.input_min`]>
        {{ toUpperTextKey('input_min') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('input_min'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:[`header.input_max`]>
        {{ toUpperTextKey('input_max') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('input_max'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:[`header.scale_min`]>
        {{ toUpperTextKey('scale_min') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('scale_min'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:[`header.scale_max`]>
        {{ toUpperTextKey('scale_max') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('scale_max'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>

      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small @click="editItem(item)" color="primary"> mdi-pencil </v-icon>
      </template>

      <template v-slot:no-data> No data</template>
      <template v-slot:[`item.created_on`]="{ item }" >
        {{item.created_on | timezone}}
      </template>
      <template v-slot:[`item.updated_on`]="{ item }" >
