      </template>
      <template v-slot:[`item.ts`]="{ item }" >
        {{item.ts | timezone}}
      </template>
    </v-data-table>

    <!-- Edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Edit</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field 
                    v-model="editedItem.name" 
                    label="Point name" 
                    required :rules="[() => !isExistingvalue(editedItem, 'name') || 'Name Existed',rules.required]" 
                    @input="(val)=>editedItem.name = conventionNaming(val, setting)"
                  />
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-checkbox v-model="editedItem.enable" label="Enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.value_operation" label="math operation" > </v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.cov_threshold" :label="toUpperTextKey('cov_threshold')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.value_original" :label="toUpperTextKey('value_original')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.value_round" :label="toUpperTextKey('value_round')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.value_raw" :label="toUpperTextKey('value_raw')"></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.input_min" :label="toUpperTextKey('input_min')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.input_max" :label="toUpperTextKey('input_max')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.scale_min" :label="toUpperTextKey('scale_min')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.scale_max" :label="toUpperTextKey('scale_max')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
