</template>

<script>
import axios from "../../axios";
import _cloneDeep from 'lodash/cloneDeep';
import _find from 'lodash/find';
import BreadcrumbsDirection from '../Common/BreadcrumbsDirection.vue';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { RULES, POINT_HEADERS, POINT_ADVANCED_HEADERS, POINT_EDIT_ALL_DEFAULT } from './loraConstant';
import { toUpperTextKey, conventionNaming } from "../../utils/utils";

export default {
  components: {
    BreadcrumbsDirection
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    conventionNaming: conventionNaming, 
    rules: {...RULES},
    tableProps: {...DATA_TABLE_PROPS},
    pointHeaders: [...POINT_HEADERS],
    advancedHeaders: [...POINT_ADVANCED_HEADERS],
    editAll: {...POINT_EDIT_ALL_DEFAULT},
    dialog: false,
    dialogMassEdit: false,
    selected: [],
    dirs: [{text: 'Devices', link: '/lora-network/devices', disabled: false}, {text: 'Points View', disabled: true}],
    search:"",
    valid: false,
    editedItem: {},
    editField: "",
    intervalId: null,
    advancedView: false,
    dataTable: [],
  }),
  computed: {
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: { },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    next();
  },
  mounted() {
    this.getAllPoints();

    //set auto refresh
    if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getAllPoints();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getAllPoints() {
      axios.get('lora/device').then(data => {
        this.getDataTable(data);
      });
    },
    getDataTable(devices){
      this.dataTable = [];
      devices.forEach(device=>{
        if(device.points.length > 0){
          const pointsPerDevice = device.points.map(point=>{
            const keys = Object.keys(point.point_store);
            if (keys && keys.length > 0) {
              keys.forEach(key => {
                point = { ...point, [key]: point.point_store[key], category: device.name };
              });
            }
            delete point.point_store;
            delete point.point_uuid;
            return point;
          });
          this.dataTable = this.dataTable.concat(pointsPerDevice);
        }
      });
    },
    updatePoint(data) {
      axios.patch(`lora/point/${data.uuid}`, data)
        .then(response => {
          const index = this.dataTable.findIndex(n => n.uuid == data.uuid);
          Object.assign(this.dataTable[index], data);
          this.$toasted.show('update success');
          this.close();
        });
    },
    isExistingvalue(item, fieldName) {
      let points = _cloneDeep(this.dataTable);
      const index = points.findIndex(p=>p.uuid == item.uuid);
      points.splice(index, 1);
      return _find(points, {[fieldName]: item[fieldName]}) != null;
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
      });
    },
    editItem(item) {
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = {...POINT_EDIT_ALL_DEFAULT};
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit() { },
    closeEdit() { },
    onClickRefresh(){
      this.getAllPoints();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    }
  },
};
</script>
